    height: 30px;
    background: #eee;
    border-bottom: 1px solid #ddd;
    font-size: 18px;
    font-weight: bold;
}
#storeDetailApp .info .edit {
    position: absolute;
    top: 6px;
    right: 44px;
    color: #888;
    height: 24px;
    width: 24px;
    cursor: pointer;
    background-image: url(data:image/png;base64,)
}
#storeDetailApp .info .save {
    position: absolute;
    top: 6px;
    right: 44px;
    color: #888;
    height: 24px;
    width: 24px;
    cursor: pointer;
    background-image: url(data:image/png;base64,)
}
#storeDetailApp .info .close {
    position: absolute;
